import React, { Component } from 'react'

class Home extends Component {
  render() {
    return (
      <div className="container">
        <p className="e-paragraph">I'm a developer who loves learn new technologies and play video games. Currently I'm in love with ReactJS.</p>
        <hr />
        <p className="e-paragraph">Main skills</p>
        <ul className="list">
          <li>JavaScript (ES6+, jQuery)</li>
          <li>React + Redux</li>
          <li>HTML</li>
          <li>CSS (SASS + LESS)</li>
          <li>NoSQL</li>
        </ul>
      </div>
    )
  }
}

export default Home;
